.sidebar {
  width: 300px;
  padding: 20px;
  background-color: #f1f4f6;
  border-radius: 0 12px 12px 0;
  position: fixed;
  left: 0;
  top: 80px;
  bottom: 0;
  height: calc(100vh - 80px);
  overflow: hidden;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 10;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
}

.sidebar.collapsed {
  width: 60px;
  padding: 20px 10px;
}

/* Ensure toggle button is always visible and properly positioned */
.sidebar-toggle-container {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 20;
}

.sidebar-content {
  width: 300px;
  height: 100%;
  overflow-y: auto;
  padding-right: 20px;
  box-sizing: border-box;
}

.sidebar.collapsed .sidebar-content {
  opacity: 0;
  transition: opacity 0.1s ease;
}

.sidebar:not(.collapsed) .sidebar-content {
  opacity: 1;
  transition: opacity 0.2s ease 0.1s; /* Slight delay for smoother appearance */
}

.sidebar-toggle {
  background: #f1f4f6;
  border: 1px solid #d9d9d9;
  font-size: 16px;
  color: #333;
  cursor: pointer;
  padding: 5px;
  border-radius: 4px;
  transition: all 0.2s;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
}

.sidebar-toggle:hover {
  background-color: #e6f7ff;
  border-color: #69c0ff;
}

/* Ensure antd icons are properly displayed */
.sidebar-toggle .anticon {
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.sidebar-toggle:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.sidebar.collapsed .filter-section > *:not(.sidebar-toggle) {
  opacity: 0;
  pointer-events: none;
}

.sidebar.collapsed .sidebar-toggle {
  position: relative;
  margin: 0 auto;
  display: block;
  width: 100%;
  text-align: center;
}

.filter-section {
  margin-bottom: 24px;
}

.search-container {
  margin-bottom: 16px;
  width:50%;
}

.search-input {
  width: 100%;
  padding: 10px 12px;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  font-size: 14px;
  transition: all 0.3s;
  box-sizing: border-box;
}

.search-input:focus {
  outline: none;
  border-color: #40a9ff;
  box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
}

.search-input::placeholder {
  color: #bfbfbf;
}

.subscription-plan-selector {
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 1px solid #f0f0f0;
}

.subscription-options {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 8px;
}

.subscription-option {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 140px;
  padding: 10px 12px;
  background: #f8f8f8;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s ease;
  text-align: left;
}

.subscription-option:hover {
  background: #f0f0f0;
  border-color: #d9d9d9;
}

.subscription-option.active {
  background: #1890ff;
  border-color: #1890ff;
  color: white;
}

.subscription-option .plan-name {
  font-weight: 500;
}

.subscription-option .plan-price {
  font-size: 0.9em;
  opacity: 0.8;
}

.subscription-option.active .plan-price {
  opacity: 1;
  font-weight: 500;
}

.sort-controls {
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 1px solid #f0f0f0;
}

.sort-options {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 8px;
}

.sort-option {
  width: 150px;
  padding: 8px 12px;
  background: #f8f8f8;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  color: #555;
  font-size: 0.9rem;
  cursor: pointer;
  text-align: center;
  transition: all 0.2s ease;
}

.sort-option:hover {
  background: #f0f0f0;
  border-color: #d0d0d0;
}

.sort-option.active {
  background: #1a1a1a;
  color: white;
  border-color: #1a1a1a;
}

.filter-title {
  font-size: 1rem;
  font-weight: 600;
  margin: 0 0 12px 0;
  color: #333;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.brand-list {
  display: flex;
  flex-wrap: wrap;
  gap: 0 30px;
  max-height: 300px;
  overflow-y: auto;
  padding-right: 8px;
  margin-right: -8px;
  align-content: flex-start;
  min-width: 400px; /* Ensure enough width for 2 columns */
}

.brand-column {
  display: flex;
  flex-direction: column;
  gap: 8px;
  min-width: 180px;
  flex: 1 0 calc(50% - 15px); /* Force 2 columns by default */
  max-width: calc(50% - 15px); /* Ensure only 2 columns fit */
}

/* Custom scrollbar for the brand list */
.brand-list::-webkit-scrollbar {
  width: 6px;
}

.brand-list::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

.brand-list::-webkit-scrollbar-thumb {
  background: #d9d9d9;
  border-radius: 3px;
}

.brand-list::-webkit-scrollbar-thumb:hover {
  background: #bfbfbf;
}

.brand-item {
  display: flex;
  align-items: center;
  padding: 6px 10px;
  cursor: pointer;
  border-radius: 6px;
  transition: background-color 0.2s ease;
  white-space: nowrap;
}

.brand-item:hover {
  background-color: #f8f8f8;
}

.brand-item:hover .brand-checkbox {
  border-color: #999;
}

.brand-checkbox {
  width: 18px;
  height: 18px;
  border: 2px solid #d9d9d9;
  border-radius: 4px;
  margin-right: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  position: relative;
  flex-shrink: 0;
  cursor: pointer;
}

.brand-item.selected .brand-checkbox {
  border-color: #1a1a1a;
  background-color: #1a1a1a;
}

.brand-checkbox svg {
  width: 12px;
  height: 12px;
  opacity: 0;
  transition: opacity 0.2s ease;
}

.brand-item.selected .brand-checkbox svg {
  opacity: 1;
}

.brand-name {
  font-size: 0.95rem;
  color: #333;
  margin-right: 6px;
}

.brand-count {
  font-size: 0.85rem;
  color: #888;
}

/* Responsive styles */
@media (max-width: 1200px) {
  .brand-list {
    gap: 0 20px;
    min-width: auto;
  }
  
  .brand-column {
    min-width: 160px;
    flex: 0 1 auto;
    max-width: none;
  }
}

@media (max-width: 1024px) {
  .sidebar {
    width: 100%;
    margin-right: 0;
    margin-bottom: 24px;
  }
  
  .brand-list {
    max-height: none;
    overflow-y: visible;
  }
}

@media (max-width: 768px) {
  .brand-list {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 0 15px;
    min-width: 100%;
  }
  
  .brand-column {
    min-width: calc(50% - 10px);
    flex: 1 0 calc(50% - 10px);
    max-width: calc(50% - 10px);
  }
}

/* For very small screens, switch to single column */
@media (max-width: 480px) {
  .brand-list {
    flex-direction: column;
    min-width: 100%;
  }
  
  .brand-column {
    min-width: 100%;
    max-width: 100%;
  }
}
